<template>
  <view class="content">
    <view class="list">
      <view class="tishi">若您想修改密码，则可在此修改</view>
      <view class="list-call">
        <image class="img" src="/static/shilu-login/1.png"></image>
        <input class="sl-input" type="number" v-model="phone" maxlength="11" placeholder="请输入手机号" />
      </view>
      <view class="list-call">
        <image class="img" src="/static/shilu-login/2.png"></image>
        <input class="sl-input" type="text" v-model="password1" maxlength="32" placeholder="请输入旧密码" :password="!showPassword" />
        <image class="img" :src="showPassword?'/static/shilu-login/op.png':'/static/shilu-login/cl.png'" @tap="display"></image>
      </view>
	  <view class="list-call">
	    <image class="img" src="/static/shilu-login/2.png"></image>
	    <input class="sl-input" type="text" v-model="password2" maxlength="32" placeholder="请输入新密码" :password="!showPassword" @confirm="mima" />
	    <image class="img" :src="showPassword?'/static/shilu-login/op.png':'/static/shilu-login/cl.png'" @tap="display"></image>
	  </view>
	  <view class="list-call">
	    <image class="img" src="/static/shilu-login/2.png"></image>
	    <input class="sl-input" type="text" v-model="password2" maxlength="32" placeholder="请再次输入新密码" :password="!showPassword" @confirm="q_mima" />
	    <image class="img" :src="showPassword?'/static/shilu-login/op.png':'/static/shilu-login/cl.png'" @tap="display"></image>
	  </view>
      <!-- <view class="list-call">
        <image class="img" src="/static/shilu-login/3.png"></image>
        <input class="sl-input" type="number" v-model="code" maxlength="4" placeholder="验证码" />
        <view class="yzm" :class="{ yzms: second>0 }" @tap="getcode">{{yanzhengma}}</view>
      </view> -->
    </view>
    <view class="button-login" hover-class="button-hover" @tap="bindLogin()">
      <text>修改密码</text>
    </view>
  </view>
</template>

<script>
  var _this, js;
  export default {
    data() {
      return {
        phone: '',
        second: 0,
        code: "",
        showPassword: false,
        password: ''
      }
    },
    onLoad() {
      _this = this;
    },
    // computed: {
    //   yanzhengma() {
    //     if (this.second == 0) {
    //       return '获取验证码';
    //     } else {
    //       if (this.second < 10) {
    //         return '重新获取0' + this.second;
    //       } else {
    //         return '重新获取' + this.second;
    //       }
    //     }
    //   }
    // },
    onUnload() {
      this.clear()
    },
    methods: {
      display() {
        this.showPassword = !this.showPassword
      },
      clear(){
        clearInterval(js)
        js = null
        this.second = 0
      },
      getcode() {
        if (this.phone.length != 11) {
          uni.showToast({
            icon: 'none',
            title: '手机号不正确'
          });
          return;
        }
        if (this.second > 0) {
          return;
        }
        _this.second = 60;
        js = setInterval(function() {
          _this.second--;
          if (_this.second == 0) {
            _this.clear()
          }
        }, 1000)
        // uni.request({
        //   url: 'http://***/getcode.html', //仅为示例，并非真实接口地址。
        //   data: {
        //     phone: this.phone,
        //     type: 'forget'
        //   },
        //   method: 'POST',
        //   dataType: 'json',
        //   success: (res) => {
        //     if (res.data.code != 200) {
        //       uni.showToast({
        //         title: res.data.msg,
        //         icon: 'none'
        //       });
        //       _this.second = 0;
        //     } else {
        //       uni.showToast({
        //         title: res.data.msg
        //       });
        //       _this.second = 60;
        //       js = setInterval(function() {
        //         _this.second--;
        //         if (_this.second == 0) {
        //           _this.clear()
        //         }
        //       }, 1000)
        //     }
        //   },fail() {
        //     this.clear()
        //   }
        // });
      },
      bindLogin() {
        if (this.phone.length != 11) {
          uni.showToast({
            icon: 'none',
            title: '手机号不正确'
          });
          return;
        }
        uniCloud.callFunction({
        		name: 'modify_secret',
        		data:{
        			PhoneNumber:this.phone,
        			old_password:this.password1,
					new_password:this.password2
        		}
        	}).then((res) => {
				res=res.result
				if(res=='bucunzai'){
					uni.showToast({
						title:'账号不存在',
						icon:'none'
					})
				} 
				else if(res=='chenggong'){
					uni.showToast({
						title:'修改成功，即将跳转到登录界面',
						icon:'none'
					})
					setTimeout(()=>{
					  uni.navigateTo({
					  	url:'../login/login'
					  })
					},2000)
				}
				else{
					uni.showToast({
						title:'密码错误',
						icon:'none'
					})
				}
        	}).catch((err) => {
        		uni.hideLoading()
        		console.error(err);
        	})
        },
		mima(event){
			yi_mima=event.detail;
			console.log(yi_mima);
		},
		q_mima(event){
			er_mima=event.detail;
			console.log(er_mima);
			if(yi_mima.value!=er_mima.value){
				uni.showModal({
					title:"两次密码不一致",
					showCancel:false
				})
			}
		}
        // uni.request({
        //   url: 'http://***/forget.html',
        //   data: {
        //     phone: this.phone,
        //     password: this.password,
        //     code: this.code
        //   },
        //   method: 'POST',
        //   dataType: 'json',
        //   success: (res) => {
        //     if (res.data.code != 200) {
        //       uni.showToast({
        //         title: res.data.msg,
        //         icon: 'none'
        //       });
        //     } else {
        //       uni.showToast({
        //         title: res.data.msg
        //       });
        //       setTimeout(function() {
        //         uni.navigateBack();
        //       }, 1500)
        //     }
        //   }
        // });

      }
    }
</script>

<style>
  .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .tishi {
    color: #999999;
    font-size: 28rpx;
    line-height: 50rpx;
    margin-bottom: 50rpx;
  }

  .list {
    display: flex;
    flex-direction: column;
    padding-top: 50rpx;
    padding-left: 70rpx;
    padding-right: 70rpx;
  }

  .list-call {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100rpx;
    color: #333333;
    border-bottom: 0.5px solid #e2e2e2;
  }

  .list-call .img {
    width: 40rpx;
    height: 40rpx;
  }

  .list-call .sl-input {
    flex: 1;
    text-align: left;
    font-size: 32rpx;
    margin-left: 16rpx;
  }

  .button-login {
    color: #FFFFFF;
    font-size: 34rpx;
    width: 470rpx;
    height: 100rpx;
    background: linear-gradient(-90deg, rgba(63, 205, 235, 1), rgba(188, 226, 158, 1));
    box-shadow: 0rpx 0rpx 13rpx 0rpx rgba(164, 217, 228, 0.2);
    border-radius: 50rpx;
    line-height: 100rpx;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100rpx;
  }

  .button-hover {
    background: linear-gradient(-90deg, rgba(63, 205, 235, 0.8), rgba(188, 226, 158, 0.8));
  }

  .yzm {
    color: #FF7D13;
    font-size: 24rpx;
    line-height: 64rpx;
    padding-left: 10rpx;
    padding-right: 10rpx;
    width: auto;
    height: 64rpx;
    border: 1rpx solid rgba(255, 131, 30, 1);
    border-radius: 50rpx;
  }

  .yzms {
    color: #999999 !important;
    border: 1rpx solid #999999;
  }
</style>
